#about{
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-section{
    width: 100%;
    
    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;

    text-align: center;

    color: var(--white);
}

.about-section > img{
    height: 10rem;
}
.about-section > div h2{
    font-size: 2rem;
    font-weight: 700;
    margin: 2rem 0;
}
.about-section > div p{
    font-size: 1.4rem;
    font-weight: 400;
}

@media screen and (max-width: 600px) {
    #about{
        flex-direction: column;
        align-items: flex-start;
    }
    .about-section{
        align-items: center;
        justify-content: space-between;
        margin-top: 1rem;
    }

    .about-section > img{
        height: 5rem;
        margin-top: 2rem;
    }
    
    .about-section > div{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }
    
    .about-section > div h2{
        font-size: 1.6rem;
        margin: 1rem 0;
    }
    .about-section > div p{
        font-size: 1rem;
    }
}